// Font
$font-size-sm: 16px;
$font-size-md: 20px;
$font-size-lg: 32px;
$font-size-xl: 38px;
$font-size-xxl: 46px;


// Color
$color-white: #fff;

$color-red: rgb(205, 2, 63);
$color-green: #00a72a;
$color-blue: rgb(87, 2, 205);

$color: rgb(205, 2, 63);

// linear gradient
$gradient: linear-gradient(
  225deg,
  #ffb359,
  #ff363f,
  #ff45b5,
  #dc2cff,
  #592bff,
  #2c5eff,
  #00ffbb,
  #01ae0f,
  #a8c200
);

$gradient-red: linear-gradient(225deg, #ffb359 -5%, #ff363f 50%, #ff45b5 105%);
// $gradient-blue: linear-gradient(225deg, #59ffe1 -5%, #4036ff 50%, #ff45f9 105%);

.app {
  &.pomodoro {
    %share-bc {
      background-color: $color-red;
    }
  }
  &.shortBreak {
    %share-bc {
      background-color: $color-green;
    }
  }
  &.longBreak {
    %share-bc {
      background-color: $color-blue;
    }
  }
}
