@import "../../variables.scss";

.progress-bar {
  width: 100%;
  border-radius: 10px;
  height: 4px;
  position: relative;
  background-color: #fff;
  cursor: pointer;
  margin-top: 7px;
}

.progress-bar__part {
  position: absolute;
  left: 0;
  top: -1px;
  height: 6px;
  border-radius: 10px;
  @extend %share-bc;
  transition: width 1s ease;
}
