.timer {
  background-color: rgba(255, 255, 255, 0.25);
  border-radius: 15px;
  padding: 15px;
  width: 80%;
  max-width: 600px;
  margin: 0 auto;

  &__list {
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
  }

  &__clock {
    text-align: center;
    color: #fff;
    font-size: 160px;
    margin: 30px 0;
  }

  &__btns {
      display: flex;
      align-items: center;
      justify-content: space-evenly;
  }
}

@media screen and (max-width: 767px) {
  .timer__clock {
    font-size: 120px;
  }
}
@media screen and (max-width: 480px) {
  .timer {
    padding: 5px;
    width: 100%;
    max-width: 340px;
  }
  .timer__clock {
    font-size: 80px;
  }
}