@import "../../variables.scss";

.history {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  color: $color-white;
  font-size: $font-size-md;
}

@media screen and (max-width: 767px) {
  .history {
    font-size: $font-size-sm;
  }
}

@media screen and (max-width: 480px) {
  .history {
    font-size: $font-size-sm * 0.9;
  }
}
